import { ClipboardErrorEnum } from '../../utils/constants';

export default function alarmTimeoutMonitor(monitor, execFn, timeout = 10000) {
  const startTime = new Date().getTime();
  const info = execFn();
  const endTime = new Date().getTime();
  if (endTime - startTime > timeout && monitor) {
    const error = new Error();
    // @ts-ignore
    error.type = ClipboardErrorEnum.PASTE_TIME_OUT;
    error.message = info;
    monitor(error);
  }
}
